import React from 'react';
import { Container } from 'react-bootstrap';
import { graphql } from 'gatsby';
import moment from 'moment-timezone';

import Layout from 'components/Layout';
import TagLabel from 'components/label/Tag';
import NameCard from 'components/card/NameCard';

import './index.scss';

export default function Template({
  data,
}: {
  data: {
    allMarkdownRemark: {
      edges: {
        node: {
          frontmatter: {
            tags: { color: string; id: string; name: string }[];
            title: string;
            date: string;
            preview?: { external?: { url?: string } }[];
            writer?: { name: string }[];
          };
          html: any;
        };
      }[];
    };
  };
}) {
  const { frontmatter, html } = data.allMarkdownRemark?.edges?.[0]?.node ?? {
    frontmatter: {},
    html: '',
  };

  return (
    <Layout>
      <div
        className='article__image'
        style={{
          backgroundImage: `url(
            ${frontmatter.preview?.[0]?.external?.url || 'https://dummyimage.com/1024x800/000/fff'}
          )`,
        }}
      />

      <Container>
        <div className='article'>
          <h1 className='article__title'>{frontmatter?.title}</h1>
          <div className='article__tags'>
            {frontmatter.tags?.map((tag, index) => (
              <TagLabel tag={tag} key={`${tag}-${index}`} />
            ))}
          </div>
          <div className='article__date'>
            {/* eslint-disable-next-line newline-per-chained-call */}
            {moment(frontmatter?.date).tz('Asia/Seoul').format('YYYY.MM.DD')}
          </div>
          <div className='markdown-body'>
            <div dangerouslySetInnerHTML={{ __html: html }} />
          </div>
        </div>

        <NameCard name={frontmatter.writer?.[0].name || ''} />
      </Container>
    </Layout>
  );
}

export const pageQuery = graphql`
  query ($articleNum: Int!) {
    allMarkdownRemark(filter: { frontmatter: { articleNum: { eq: $articleNum } } }) {
      edges {
        node {
          frontmatter {
            articleNum
            title
            date
            tags {
              color
              id
              name
            }
            preview {
              external {
                url
              }
            }
            writer {
              name
            }
          }
          html
        }
      }
    }
  }
`;
